import React, { useState } from 'react';
import {
  Container,
  Typography,
  List,
  Chip,
  Box,
  Paper,
  Collapse,
  Grid,
  Checkbox,
  Button,
} from '@mui/material';
import { styled } from '@mui/material/styles';
import { TransitionGroup } from 'react-transition-group';
import AssignmentIcon from '@mui/icons-material/Assignment';

const initialTasks = [
  { id: 1, title: 'Morning Workout', time: '06:30', duration: 60, priority: 'High' },
  { id: 2, title: 'Team Standup', time: '09:00', duration: 30, priority: 'Medium' },
  { id: 3, title: 'Client Meeting', time: '11:00', duration: 90, priority: 'High' },
  { id: 4, title: 'Project Work', time: '13:00', duration: 120, priority: 'Medium' },
  { id: 5, title: 'Lunch Break', time: '12:30', duration: 60, priority: 'Low' },
  { id: 6, title: 'Code Review', time: '15:30', duration: 90, priority: 'High' },
  { id: 7, title: 'Design Meeting', time: '17:00', duration: 60, priority: 'Medium' },
  { id: 8, title: 'Send Report', time: '18:30', duration: 30, priority: 'Low' },
  { id: 9, title: 'Evening Run', time: '19:00', duration: 60, priority: 'Low' },
  { id: 10, title: 'Dinner with Family', time: '20:30', duration: 90, priority: 'High' },
];

const priorityColors = {
  High: 'error',
  Medium: 'warning',
  Low: 'info',
};

const statusOrder = ['Finished', 'In Progress', 'Upcoming', 'Overdue'];

const statusColors = {
  Finished: 'success',
  'In Progress': 'info',
  Upcoming: 'primary',
  Overdue: 'error',
};

const formatTime = (timeString) => {
  const [hourString, minuteString] = timeString.split(':');
  let hour = parseInt(hourString, 10);
  const minute = minuteString;
  const ampm = hour >= 12 ? 'PM' : 'AM';
  hour = hour % 12;
  if (hour === 0) hour = 12;
  return `${hour}:${minute} ${ampm}`;
};

const formatDateTime = (dateObj) => {
    return dateObj.toLocaleTimeString('en-US', {
      hour: 'numeric',
      minute: '2-digit',
    });
  };
  
  const formatDate = (dateObj) => {
    return dateObj.toLocaleDateString('en-US', {
      weekday: 'long',
      month: 'long',
      day: 'numeric',
      year: 'numeric',
    });
  };
  

// Styled components with consistent layout for TaskItem
const TaskItem = styled(Paper)(({ theme, status }) => ({
  padding: theme.spacing(2),
  marginBottom: theme.spacing(2),
  display: 'flex',
  alignItems: 'center',
  transition: 'transform 0.4s, opacity 0.4s, height 0.4s', // Animation properties
  backgroundColor:
    theme.palette[statusColors[status]]?.light || theme.palette.background.paper,
  color: theme.palette.getContrastText(
    theme.palette[statusColors[status]]?.light || theme.palette.background.paper
  ),
  opacity: status === 'Finished' ? 0.5 : 1, // Reduce opacity only when finished
  transform: status === 'Finished' ? 'translateX(0) scale(0.95)' : 'translateX(0) scale(1)',
  '&:hover': {
    transform: status === 'Finished' ? 'scale(0.98)' : 'scale(1.02)',
  },
}));

const TaskDetails = styled(Box)({
  flexGrow: 1,
  marginLeft: '16px',
});

const StatusHeader = styled(Typography)(({ theme, status }) => ({
  backgroundColor:
    theme.palette[statusColors[status]]?.main || theme.palette.background.default,
  color: theme.palette.getContrastText(
    theme.palette[statusColors[status]]?.main || theme.palette.background.default
  ),
  padding: theme.spacing(1),
  borderRadius: theme.shape.borderRadius,
}));

const ListContainer = styled(Box)({
  maxHeight: 450,
  overflowY: 'auto',
  paddingRight: '8px',
  paddingLeft: '8px',

});

const TaskSchedule = () => {
  const [tasks, setTasks] = useState(initialTasks);

  const todayDate = new Date();
  const todayDateString = todayDate.toISOString().split('T')[0];
  const currentTime = new Date();

  const tasksWithStatus = tasks.map((task) => {
    const dueDate = todayDateString;
    const startDateTime = new Date(`${dueDate}T${task.time}`);
    const endDateTime = new Date(startDateTime.getTime() + task.duration * 60000);

    let status = task.status || '';

    if (status !== 'Finished') {
      if (currentTime < startDateTime) {
        status = 'Upcoming';
      } else if (currentTime >= startDateTime && currentTime < endDateTime) {
        status = 'In Progress';
      } else if (currentTime >= endDateTime) {
        status = 'Overdue';
      }
    }

    return { ...task, dueDate, startDateTime, endDateTime, status };
  });

  const handleCheckboxChange = (taskId) => {
    setTasks((prevTasks) =>
      prevTasks.map((task) =>
        task.id === taskId
          ? { ...task, status: task.status === 'Finished' ? '' : 'Finished' }
          : task
      )
    );
  };

  const handleSaveChanges = () => {
    console.log('Changes saved:', tasks);
    alert('Changes have been saved successfully!');
  };

  const tasksByStatus = tasksWithStatus.reduce((groups, task) => {
    const status = task.status;
    if (!groups[status]) {
      groups[status] = [];
    }
    groups[status].push(task);
    return groups;
  }, {});

  const formattedDate = formatDate(todayDate);

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography variant="h3" gutterBottom align="center">
        Task Schedule
      </Typography>
      <Typography variant="h4" gutterBottom align="center">
        Today - {formattedDate}
      </Typography>
      <Grid container spacing={2}>
        {statusOrder.map((status) => {
          const tasksInStatus = tasksByStatus[status] || [];
          return (
            <Grid item xs={12} sm={6} md={3} key={status}>
              <StatusHeader variant="h5" gutterBottom align="center" status={status}>
                {status}
              </StatusHeader>
              <ListContainer>
                {tasksInStatus.length > 0 ? (
                  <List>
                    <TransitionGroup>
                      {tasksInStatus.map((task) => (
                        <Collapse key={task.id}>
                          <TaskItem elevation={3} status={task.status}>
                            <Checkbox
                              checked={task.status === 'Finished'}
                              onChange={() => handleCheckboxChange(task.id)}
                              color="default"
                            />
                            <AssignmentIcon color="inherit" />
                            <TaskDetails>
                              <Typography
                                variant="h6"
                                style={{
                                  textDecoration:
                                    task.status === 'Finished' ? 'line-through' : 'none',
                                }}
                              >
                                {task.title}
                              </Typography>
                              <Typography variant="body1">
                                Time: <br></br>{formatTime(task.time)} -{' '}
                                {formatDateTime(task.endDateTime)}
                              </Typography>
                            </TaskDetails>
                            <Chip
                              label={task.priority}
                              color={priorityColors[task.priority]}
                              size="medium"
                            />
                          </TaskItem>
                        </Collapse>
                      ))}
                    </TransitionGroup>
                  </List>
                ) : (
                  <Typography align="center">No tasks in this category.</Typography>
                )}
              </ListContainer>
            </Grid>
          );
        })}
      </Grid>
      {/* Generate Schedule Button */}
      <Box display="flex" justifyContent="center" marginTop={3}>
          <Button
            variant="contained"
            color="primary"
            onClick={handleSaveChanges}
            style={{
              padding: '18px 36px',
              borderRadius: 8,
              fontSize: '1.2rem',
            }}
          >
            Save Changes
          </Button>
        </Box>
    </Container>
  );
};

export default TaskSchedule;
