import React from "react";
import { Routes, Route, useNavigate } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Home from './compo/Home';
import Profile from './compo/Profile';
import Navbar from './nav';
import Logout from './compo/Logout';
import ProtectedRoute from './compo/ProtectedRoute';
import { faFacebookF, faTwitter, faLinkedinIn, faInstagram } from '@fortawesome/free-brands-svg-icons';
import * as Components from './compo/Components';
import ShowSchedule from './compo/Schedule';
import TaskEntry from './compo/TaskEntry';
import session from '../src/back/email.php';


function App() {
    const [signIn, toggle] = React.useState(true);
    const [signUpMessage, setSignUpMessage] = React.useState('');
    const [signUpMessageType, setSignUpMessageType] = React.useState(''); 
    const [signInMessage, setSignInMessage] = React.useState(''); 
    const [signInMessageType, setSignInMessageType] = React.useState(''); 
    const navigate = useNavigate(); 

    // Authentication state
    const [isAuthenticated, setIsAuthenticated] = React.useState(
        localStorage.getItem('isAuthenticated') === 'true'
    );

    const handleSignUp = async (event) => {
        event.preventDefault();

        if (!event.target.name.value || !event.target.email.value || !event.target.password.value) {
            setSignUpMessageType('error');
            setSignUpMessage('All fields are required. Please fill in all the details.');
            return;
        }

        try {
            const response = await fetch('http://localhost/SWE-444/my-app/src/back/register.php', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                },
                body: new URLSearchParams({
                    name: event.target.name.value,
                    email: event.target.email.value,
                    password: event.target.password.value,
                }),
            });
            const data = await response.json();

            if (data.status === 'success') {
                setSignUpMessageType('success');
                setSignUpMessage('User registered successfully. Please sign in.');
                toggle(true);
            } else if (data.status === 'email_exists') {
                setSignUpMessageType('error');
                setSignUpMessage('This email is already registered. Please use a different email or sign in.');
            } else {
                setSignUpMessageType('error');
                setSignUpMessage(data.message);
            }
        } catch (error) {
            setSignUpMessageType('error');
            setSignUpMessage('An error occurred. Please try again.');
        }
    };

    const handleSignIn = async (event) => {
        event.preventDefault();
    
        if (!event.target.email.value || !event.target.password.value) {
            setSignInMessageType('error');
            setSignInMessage('Email and password are required. Please complete all fields.');
            return;
        }
    
        try {
            const response = await fetch('http://localhost/SWE-444/my-app/src/back/login.php', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded',
                },
                body: new URLSearchParams({
                    email: event.target.email.value,
                    password: event.target.password.value,
                }),
            });
            const data = await response.json();
    
            if (data.status === 'success') {
                setIsAuthenticated(true);
                localStorage.setItem('isAuthenticated', 'true'); // Persist login state
                localStorage.setItem('user_email', event.target.email.value); // Save email in localStorage
                navigate('/home');
            } else {
                setSignInMessageType('error');
                setSignInMessage(data.message);
            }
        } catch (error) {
            setSignInMessageType('error');
            setSignInMessage('An error occurred. Please try again.');
        }
    };
    

    return (
        <>
            <Components.GlobalStyle />
            <Routes>
                {/* Public Route */}
                <Route
                    path="/"
                    element={
                        <>
                            <Components.Wrapper>
                                <Components.Container>
                                    <Components.SignInContainer signinIn={signIn}>
                                        {signInMessage && (
                                            <Components.Message type={signInMessageType} style={{ marginBottom: '10px' }}>
                                                {signInMessage}
                                            </Components.Message>
                                        )}
                                        <Components.Form onSubmit={handleSignIn}>
                                            <Components.Title>Sign in</Components.Title>
                                            <Components.Input type='email' name='email' placeholder='Email' />
                                            <Components.Input type='password' name='password' placeholder='Password' />
                                            <Components.Anchor href='#'>Forgot your password?</Components.Anchor>
                                            <Components.Button type='submit'>Sign In</Components.Button>
                                        </Components.Form>
                                    </Components.SignInContainer>

                                    <Components.SignUpContainer signinIn={signIn}>
                                        {signUpMessage && (
                                            <Components.Message type={signUpMessageType} style={{ marginBottom: '10px' }}>
                                                {signUpMessage}
                                            </Components.Message>
                                        )}
                                        <Components.Form onSubmit={handleSignUp}>
                                            <Components.Title>Create Account</Components.Title>
                                            <Components.Input type='text' name='name' placeholder='Name' />
                                            <Components.Input type='email' name='email' placeholder='Email' />
                                            <Components.Input type='password' name='password' placeholder='Password' />
                                            <Components.Button type='submit'>Sign Up</Components.Button>
                                        </Components.Form>
                                    </Components.SignUpContainer>

                                    <Components.OverlayContainer signinIn={signIn}>
                                        <Components.Overlay signinIn={signIn}>
                                            <Components.LeftOverlayPanel signinIn={signIn}>
                                                <Components.Title customColor="#3498db">Welcome Back!</Components.Title>
                                                <Components.Paragraph customColor="#ffffff">
                                                    To keep connected with us please login with your personal info
                                                </Components.Paragraph>
                                                <Components.Button onClick={() => toggle(true)}>
                                                    Sign In
                                                </Components.Button>
                                            </Components.LeftOverlayPanel>

                                            <Components.RightOverlayPanel signinIn={signIn}>
                                                <Components.Title customColor="#3498db">Hello, Friend!</Components.Title>
                                                <Components.Paragraph>
                                                    Enter your personal details and start your journey with us
                                                </Components.Paragraph>
                                                <Components.Button onClick={() => toggle(false)}>
                                                    Sign Up
                                                </Components.Button>
                                            </Components.RightOverlayPanel>
                                        </Components.Overlay>
                                    </Components.OverlayContainer>
                                </Components.Container>
                            </Components.Wrapper>

                            <Components.Footer>
                                <div className="footer-section footer-logo">
                                    © 2024 Schedule Generator. All rights reserved.
                                </div>

                                <div className="footer-section footer-links">
                                    <a href="/terms">Terms of Service</a>
                                    <a href="/privacy">Privacy Policy</a>
                                    <a href="/contact">Contact Us</a>
                                </div>

                                <div className="footer-section footer-socials">
                                    <a href="https://facebook.com" target="_blank" rel="noreferrer">
                                        <FontAwesomeIcon icon={faFacebookF} />
                                    </a>
                                    <a href="https://twitter.com" target="_blank" rel="noreferrer">
                                        <FontAwesomeIcon icon={faTwitter} />
                                    </a>
                                    <a href="https://linkedin.com" target="_blank" rel="noreferrer">
                                        <FontAwesomeIcon icon={faLinkedinIn} />
                                    </a>
                                    <a href="https://instagram.com" target="_blank" rel="noreferrer">
                                        <FontAwesomeIcon icon={faInstagram} />
                                    </a>
                                </div>
                            </Components.Footer>
                        </>
                    }
                />

                {/* Protected Routes */}
                <Route
                    path="/home"
                    element={
                        <ProtectedRoute isAuthenticated={isAuthenticated}>
                            <>
                                <Navbar />
                                <Home />
                            </>
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/home"
                    element={
                        <ProtectedRoute isAuthenticated={isAuthenticated}>
                            <>
                                <Navbar />
                                <session />
                            </>
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/profile"
                    element={
                        <ProtectedRoute isAuthenticated={isAuthenticated}>
                            <>
                                <Navbar />
                                 <Profile /> 
                            </>
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/schedule"
                    element={
                        <ProtectedRoute isAuthenticated={isAuthenticated}>
                            <>
                                <Navbar />
                                <ShowSchedule />
                            </>
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/TaskEntry"
                    element={
                        <ProtectedRoute isAuthenticated={isAuthenticated}>
                            <>
                                <Navbar />
                                <TaskEntry />
                            </>
                        </ProtectedRoute>
                    }
                />
                <Route
                    path="/logout"
                    element={
                        <Logout setIsAuthenticated={setIsAuthenticated} />
                    }
                />
            </Routes>
        </>
    );
}

export default App;
