import React, { useState, useEffect } from 'react';
import '../style/Profile.css';

function UserProfile() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [message, setMessage] = useState('');
  const [messageType, setMessageType] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();

    if (password !== confirmPassword) {
      setMessage("Passwords don't match.");
      setMessageType('error');
      return;
    }

    const formData = new URLSearchParams();
    formData.append('password', password);
    formData.append('confirm_password', confirmPassword);

    fetch('http://localhost/SWE-444/my-app/src/tempProfile.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
      credentials: 'include', // Include cookies in the request
      body: formData.toString(),
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.success) {
          setMessage('Password has been updated successfully.');
          setMessageType('success');
        } else {
          setMessage(data.message || 'Error updating password.');
          setMessageType('error');
        }
      })
      .catch((error) => {
        console.error('Error:', error.message);
        setMessage('An error occurred.');
        setMessageType('error');
      });
  };

  return (
    <div className="App">
      <div className="profile-container">
        <div className="profile-header">
          <img src="/icon.png" alt="Profile" className="profile-img" />
          <h2>Profile Management</h2>
        </div>

        {message && (
          <p className={`message ${messageType === 'error' ? 'error' : 'success'}`}>
            {message}
          </p>
        )}

        <div className="form-grid">
          <form onSubmit={handleSubmit} className="form-section">
            <div className="form-group">
              <label htmlFor="email">Email</label>
              {/* Display email from the server-side session */}
              <input
                type="email"
                id="email"
                value={email}
                disabled // Make the email field read-only
              />
            </div>
            <div className="form-group">
              <label htmlFor="password">New Password</label>
              <input
                type="password"
                id="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                required
              />
            </div>
            <div className="form-group">
              <label htmlFor="confirm-password">Confirm Password</label>
              <input
                type="password"
                id="confirm-password"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                required
              />
            </div>
            <button type="submit" className="btn-primary">Update Password</button>
          </form>
        </div>
      </div>
    </div>
  );
}

export default UserProfile;

